.container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f7e09988 0%, #ffcc0082 100%);

}

.hero {
  padding: 6rem 0 4rem; /* Reduzido de 8rem para 6rem */
  text-align: center;
}

.heroContent {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
}

.title {
  font-size: clamp(2.5rem, 6vw, 4rem);
  font-weight: 800;
  color: #111827;
  margin-bottom: 1rem;
}

.subtitle {
  font-size: 1.5rem;
  color: #a78a15;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.intro {
  font-size: 1.125rem;
  color: #4b5563;
  line-height: 1.7;
}

.servicesSection {
  padding: 4rem 0;
  background: white;
}

.sectionContainer {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.servicesGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.serviceCard {
  background: linear-gradient(135deg, #fceab388 0%, #f8ebb482 100%);

  padding: 2rem;
  border-radius: 1.5rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.serviceCard:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px #a78a1592;
}

.serviceCardActive {
  border-color: #ffcf0f;
  background: white;
  box-shadow: 0 10px 25px #ffcf0fb0;
}

.serviceIcon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.serviceTitle {
  font-size: 1.25rem;
  font-weight: 700;
  color: #111827;
  margin-bottom: 1rem;
}

.serviceDescription {
  color: #4b5563;
  line-height: 1.6;
}

.serviceDetails {
  padding: 4rem 0;
  /* background: linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 100%); */
  background: linear-gradient(135deg, #f7e09988 0%, #ffcc0082 100%);
  background: linear-gradient(135deg, #fceab388 0%, #f8ebb482 100%);

  /* background: linear-gradient(135deg, #f8e2a0 0%, #fed637 100%); */

}

.detailsContent {
}

.detailsGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.detailsText {
}

.detailsTitle {
  font-size: 2.5rem;
  font-weight: 700;
  color: #111827;
  margin-bottom: 1.5rem;
}

.detailsDescription {
  font-size: 1.125rem;
  color: #4b5563;
  line-height: 1.7;
  margin-bottom: 2rem;
}

.featuresList {
}

.featuresTitle {
  font-size: 1.25rem;
  font-weight: 600;
  color: #111827;
  margin-bottom: 1rem;
}

.features {
  list-style: none;
  padding: 0;
  margin: 0;
  
}

.feature {
  color: #001452;
  font-weight: 500;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 300;
}

.detailsImage {
}

.serviceImage {
  width: 100%;
  height: auto;
  border-radius: 1rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

.cta {
  padding: 4rem 0;
  background: linear-gradient(135deg, #a78a15 0%, #7e6605 100%);
  /* background: linear-gradient(135deg, #22c55e 0%, #10b981 100%); */
  color: white;
}

.ctaContainer {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
}

.ctaContent {
  text-align: center;
}

.ctaTitle {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.ctaDescription {
  font-size: 1.125rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.ctaButton {
  background: white;
  color: #a78a15;
  padding: 1rem 2rem;
  border: none;
  border-radius: 50px;
  font-size: 1.125rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.ctaButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
}

.nav {
  display: flex;
  margin-bottom: 2rem;
  width: 100%;
  justify-content: center;
  gap: 10px;
}

.navIten {
  /* background: linear-gradient(135deg, #fceab388 0%, #f8ebb482 100%); */
  padding: 10px 20px;
  border-radius: 20px;
  font-weight: 400;
  border: 1px solid #7e6605;
  cursor: pointer;
  transition: all .3s;
  color: #7e6605;
}

.navIten:hover {
  color: #fff;
  background-color: #7e6605;
}

.active {
  color: #fff;
  background-color: #7e6605;
}

@media (max-width: 768px) {
  .detailsGrid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .servicesGrid {
    grid-template-columns: 1fr;
  }
}
